﻿.navbar {
  background-color: var(--navbar-bg);
  .navbar-brand {
    margin: 0;
    img {
      width: 345px;
    }
  }
  .navbar-collapse {
    flex-direction: column;
    align-items: flex-start;
    padding-#{$end}: 60px;
    padding-#{$start}: 40px;
    .navbar-links {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      border-bottom: 1px solid var(--dark-color);
      padding-bottom: 30px;
      font-weight: bold;
      color: var(--dark-color);
      .navbar-links-container {
        align-items: center;
        .nav-item {
          .nav {
            .nav-item {
              .nav-link {
                color: var(--dark-color);
              }
            }
          }
          &.social-options {
            margin-#{$start}: 40px;
            .nav-link {
              padding: 0 5px;
            }
          }
          &.font-options {
            font-size: 11px;
            .nav-link {
              padding: 0 10px;
            }
            .reset-font {
              .nav-link {
                display: flex;
                justify-content: center;
                align-items: center;
                .icon-refresh {
                    position: absolute;
                    font-size: 15px;
                }
              }
            }
          }
          &.hotline {
            margin-#{$start}: 30px;
            span {
              display: flex;
              align-items: center;
              font-size: 20px;
              .icon-phone {
                font-size: 25px;
                margin-#{$end}: 5px;
              }
            }
          }
          &.search {
            margin-#{$start}: 30px;
            .icon-search {
              font-size: 25px;
            }
          }
        }
      }
    }
    .navbar-nav-items {
      display: flex;
      justify-content: space-between;
      width: 100%;
      .navbar-nav {
        padding-top: 15px;
        .nav-item {
          .nav-link {
            color: var(--dark-color);
            padding: 0;
            margin-#{$end}: 15px;
          }
        }
      }
      .login {
        background-color: var(--dark-color);
        display: flex;
        a {
          color: var(--light-color);
          text-decoration: none;
          font-size: 16px;
          padding: 10px 40px;
        }
      }
    }
  }
}


/* The switch - the box around the slider */
.switch {
    position: relative;
    display: inline-block;
    width: 45px;
    height: 20px;
  }
  
  /* Hide default HTML checkbox */
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  /* The slider */
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--dark-color);
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 20px;
    width: 20px;
    left: 0px;
    bottom: 0px;
    background-color: var(--light-color);
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: var(--dark-color);
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
  }
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }